import React from 'react';
import { useParams, Link } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import { caseData } from '../data/caseData';
import { teamData } from '../data/teamData';
import { ROUTES } from '../config/routes';

// 複用轉換函數
const convertToChineseNumber = (amount: number): string => {
  const units = ['', '萬', '億', '兆'];
  let result = '';
  
  if (amount === 0) return '0元';
  
  let temp = amount;
  let unitIndex = 0;
  
  while (temp >= 1) {
    const part = temp % 10000;
    if (part !== 0) {
      result = part + units[unitIndex] + result;
    }
    temp = Math.floor(temp / 10000);
    unitIndex++;
  }
  
  return result + '元';
};

const CaseDetail: React.FC = () => {
  const { id } = useParams();
  const estate = caseData.find(c => c.id.toString() === id);
  
  if (!estate) {
    return (
      <div className="container mx-auto p-4">
        <h1 className="text-2xl font-bold mb-4">案例不存在</h1>
        <Link to="/success" className="text-blue-600 hover:text-blue-800">
          返回案例列表
        </Link>
      </div>
    );
  }

  const team = teamData.find(t => 
    t.companyName.includes(estate.lendingTeam) || 
    `${t.location}${t.name}` === estate.lendingTeam
  );

  return (
    <div className="container mx-auto p-4">
      <Helmet>
        <title>{`${estate.area} - 案例詳情`}</title>
        <meta name="description" content={`${estate.area}房貸案例詳情，位於${estate.address}，${estate.propertyType}類型物件。`} />
      </Helmet>

      <div className="mb-4">
        <Link to={ROUTES.SUCCESS_CASES} className="text-blue-600 hover:text-blue-800">
          ← 返回案例列表
        </Link>
      </div>

      <div className="bg-white rounded-lg shadow-lg p-6">
        <h1 className="text-3xl font-bold mb-6">{estate.area}</h1>

        <div className="space-y-6">
          <div className="bg-gray-50 p-4 rounded-lg">
            <h2 className="text-xl font-semibold mb-4">基本資訊</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <p><span className="font-medium">地址：</span>{estate.address}</p>
              <p><span className="font-medium">類型：</span>{estate.propertyType}</p>
              <p><span className="font-medium">屋齡：</span>{estate.buildingAge} 年</p>
              <p><span className="font-medium">樓高：</span>{estate.floors} 樓</p>
              <p><span className="font-medium">車位：</span>{estate.parking ? '有' : '無'}</p>
              <p><span className="font-medium">建物坪數：</span>{estate.buildingSize} 坪</p>
            </div>
          </div>

          <div className="bg-blue-50 p-4 rounded-lg">
            <h2 className="text-xl font-semibold mb-4">放款資訊</h2>
            <div className="space-y-3">
              {estate.lendingTeam && (
                <p>
                  <span className="font-medium">放款團隊：</span>
                  {team ? (
                    <Link 
                      to={`/team/${team.id}`}
                      className="text-blue-600 hover:text-blue-800"
                    >
                      {estate.lendingTeam}
                    </Link>
                  ) : (
                    estate.lendingTeam
                  )}
                </p>
              )}
              <p>
                <span className="font-medium">放款金額：</span>
                <span className="text-blue-600">
                  {convertToChineseNumber(Number(estate.investmentAmount))}
                </span>
              </p>
              <p>
                <span className="font-medium">年化報酬：</span>
                <span className="text-green-600">
                  {estate.annualReturn}%
                </span>
              </p>
            </div>
          </div>

          <div className="bg-green-50 p-4 rounded-lg">
            <h2 className="text-xl font-semibold mb-4">擔保品資訊</h2>
            <div className="space-y-3">
              <p><span className="font-medium">持有類型：</span>{estate.collateralType}</p>
              <p><span className="font-medium">持份比例：</span>{estate.collateralRatio}%</p>
              <p>
                <span className="font-medium">估價：</span>
                <span className="text-purple-600">
                  {convertToChineseNumber(estate.appraisalValue)}
                </span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CaseDetail; 