export const convertToChineseNumber = (amount: number): string => {
  const units = ['', '萬', '億', '兆'];
  let result = '';
  
  if (amount === 0) return '0元';
  
  let temp = amount;
  let unitIndex = 0;
  
  while (temp >= 1) {
    const part = temp % 10000;
    if (part !== 0) {
      result = part + units[unitIndex] + result;
    }
    temp = Math.floor(temp / 10000);
    unitIndex++;
  }
  
  return result + '元';
}; 