import React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import { staticData } from '../data/staticData';
import { TeamMember } from '../types/Team';

const TeamMemberDetail: React.FC = () => {
  const { id = '' } = useParams();
  const navigate = useNavigate();
  const teamMembers = staticData.team;
  const member = teamMembers.find(m => m.id.toString() === id);

  if (!member) {
    return (
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-2xl text-center">找不到此團隊成員</h1>
        <div className="text-center mt-4">
          <button
            onClick={() => navigate('/team')}
            className="bg-blue-500 text-white px-4 py-2 rounded"
          >
            返回團隊列表
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Helmet>
        <title>{member.name} - 團隊成員詳細資訊</title>
      </Helmet>

      <div className="max-w-3xl mx-auto bg-white rounded-lg shadow-lg p-8">
        <div className="mb-6">
          <button
            onClick={() => navigate('/team')}
            className="text-blue-500 hover:text-blue-700"
          >
            ← 返回團隊列表
          </button>
        </div>

        <h1 className="text-3xl font-bold mb-6">{member.name}</h1>
        <div className="grid md:grid-cols-2 gap-8">
          <div>
            <h2 className="text-xl font-semibold mb-4">基本資訊</h2>
            <p className="mb-2"><span className="font-medium">職稱：</span>{member.title}</p>
            <p className="mb-2"><span className="font-medium">公司：</span>{member.companyName}</p>
            <p className="mb-2"><span className="font-medium">地區：</span>{member.location}</p>
          </div>
          <div>
            <h2 className="text-xl font-semibold mb-4">業務統計</h2>
            <p className="mb-2"><span className="font-medium">已完成案件：</span>{member.casesCompleted} 件</p>
            <p className="mb-2">
              <span className="font-medium">累計放款金額：</span>
              {typeof member.totalLoanAmount === 'number' ?
                `${member.totalLoanAmount.toLocaleString()} 元` :
                '資料錯誤'}
            </p>
          </div>
        </div>

        <div className="mt-8">
          <h2 className="text-xl font-semibold mb-4">專長領域</h2>
          <div className="flex flex-wrap gap-2">
            {member.specialties.map((specialty: string) => (
              <span
                key={specialty}
                className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm"
              >
                {specialty}
              </span>
            ))}
          </div>
        </div>

        <div className="mt-8">
          <h2 className="text-xl font-semibold mb-4">簡介</h2>
          <p className="text-gray-700 whitespace-pre-line">{member.description}</p>
        </div>
      </div>
    </div>
  );
};

export default TeamMemberDetail; 